@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500&display=swap');
:root {
    --primary: #fff;
}
    .btn {
        padding: 8px 20px; 
        border-radius: 2px;
        outline: none;
        border: none;
        cursor: pointer;
        font-family: 'Poppins', sans-serif;
    }

    .btn--primary {
        background-color: var(--primary);
        color: #242424;
        border: 1px solid var(--primary);
    }

    .btn--outline {
        background-color: transparent;
        color: #3B3667;
        padding: 20px 20px;
        transition: all 0.3s ease-out;
        box-shadow: 2px 2px 4px #3B3667;
    }

    .btn--outline--navbar {
        background-color: transparent;
        color: white;
        padding: 20px 20px;
        border: 1px solid;
        transition: all 0.3s ease-out;
    }

    .btn--create-new-meeting {
        background-color:  #f2f3f4;
        color:  #3B3667;
    }
    .btn--size--navbar{
        padding: 8px 20px;
        font-size: 16px;
    }

    .btn--medium{
        padding: 8px 20px;
        font-size: 16px;
    }

    .btn--large{
        padding: 12px 26px;
        font-size: 20px;
        border-radius: 10px;
    }
    .btn--size--dashboard-large {
        padding: 10px 45px;
        font-size: 15px;
        font-weight: 500;
        border-radius: 20px;
        box-shadow: 1px 1px 4px #3B3667;
        
    }

    .btn--medium:hover, .btn--large:hover {
        background:#3B3667;
        color: white;
        transition: all 0.3s ease-out;
        box-shadow: 0px 0px 0px #3B3667;
    }

    .btn--size--dashboard-large:hover {
        color: #AEC5EB;
        transition: all 0.3;
        box-shadow: 0px 0px 0px;
 

    }
    .btn--size--navbar:hover {
        background:#AEC5EB;
        color: #3B3667;
        transition: all 0.3s ease-out;
    }

   
 

    .btn--submit {
        color: #3B3667;
        transition: all 0.3s ease-out;
        font-size: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 10px;
        background-color: #f2f3f4;
        
        
    }
    .btn--large-submit { 
        width: 60px;
        height: 70px;
        border-radius: 100%;

    }
    .btn--large-submit:hover { 
        color: #AEC5EB;
    }

    .btn-cm-exit {
        background-color: #f1f2f3;
        color: #3B3667;
        border-radius: 10px;

    }
    .btn-medium-exit:hover {
        color: #AEC5EB;
    }

    .btn-medium-exit {
        width: 50px;

    }


    .platform-buttons {
        border-radius: 20px;
        background: white;
       
    }

    .platform-buttons-size {
        width: 100px;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .platform-buttons:hover {
        border-radius: 20px;
        background: #AEC5EB;
       
    }
    .platform-buttons:active {
        border-radius: 20px;
        background: #AEC5EB;
       
    }
    .platform-buttons:focus {
        border-radius: 20px;
        background: #AEC5EB;
       
    }


    .nm-submit-button {
        background-color: white;
        font-size: 20px;
        color: #3B3667;
        box-shadow: 2px 2px 10px  #a1a2a3;


    }

    .nm-submit-button-size {
        border-radius: 20px;
        width: 175px;
    }

    .nm-submit-button:hover{
        background-color: #3B3667;
        font-size: 20px;
        color: white;
        box-shadow: 0px 0px 0px;


    }

