@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500&display=swap');




.home-page-cards-wrapper{
    padding-top: 200px;
    width: 100%;
}

.our-purpose-card {
    width: 100%;
    height: 100%px;
    display: flex;

}

.our-purpose-card-content-container { 
    height: 400px;
    display: flex;
}

.our-purpose-title-container {
    background-color: #3B3667;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 40px;
    color: white;
    box-shadow: 0px 0px 2px 2px #3B3667;

}

.our-purpose-title-container-smaller {
    width: 90%;
    height: 80%;
    text-align: center;
    padding-top: 20%;
    padding-left: 10px;
    padding-right: 10px;
    border-right: 2px solid white ;
    border-left: 2px solid white;

}

.home-page-card-title {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 40px;
}

.our-purpose-text-container {
    width: 50%;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 5%;
    padding-bottom: 5%;
    border-top: 3px solid #3B3667;
    border-bottom: 3px solid #3B3667;
    overflow-y: scroll;
}

.our-purpose-card-content {
    padding-top: 20px;
    padding-bottom: 20px;
    color: #3B3667;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    font-size: 18px;
    font-weight: 200;
    background: white;
    

}

.our-product-card {
    padding-top: 250px;
}

.our-product-card-content-container {
    font-family: 'Poppins', sans-serif;
    background: #3B3667;
    padding-top: 50px;
    height: 400px;
    display: flex;
    flex-direction: column;

}
.our-product-tagline{
    color: white;
    padding-bottom: 20px;
    padding-left: 50px;
    padding-right: 20px;
    font-weight: 200;
    font-size: 20px;
}

.our-product-title-container {
    background: white;
    padding-left: 50px;
    padding-right: 20px;
    color: #3B3667; 
}

.our-product-card-title{
    
    font-style: italic;
    font-weight: 300;
    font-size: 35px;
}

.our-product-card-content {
    background: #3B3667;
    padding-top: 20px;
    color: white;
    padding-bottom: 50px;
    padding-left: 50px;
    padding-right: 20px;
    font-weight: 200;
    font-size: 20px;
}


.about-us-card {
    width: 100%;
    height: 1000px;
    padding-top: 200px;
    padding-bottom: 200px;
}

.about-us-card-content-container {
    width: 100%;
    height: 100%;
}

.about-us-title-container {
    width: 100%;
    height: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-family: 'Poppins', sans-serif;
}
.about-us-card-title {
    font-weight: 400;
    color: #3B3667;
    font-size: 50px;

}

.the-team-content-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
}
.about-us-left-side {
    width: 50%;
    display: flex;
    flex-direction: column;
    
}

.left-photo-container{
    order: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #AEC5EB;
    border-right: 30px solid #3B3667;
}

.about-us-left-name {
    font-family: 'Poppins', sans-serif;
    text-align: right;
    padding-right: 20px;
    color: #3B3667;
    font-weight: 300;
}

.sneha-about-content-container {
    order: 1;
    height: 100%;
    background-color: #3B3667;

}

.about-us-right-side {
    width: 50%;
    display: flex;
    flex-direction: column;
    background-color: #3B3667;

}

.niral-about-content-container {
    order: 1;
    height: 100%;
}
.right-photo-container {
    order: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #AEC5EB;
    border-left: 30px solid #3B3667;
}

.about-us-right-name {
    font-family: 'Poppins', sans-serif;
    text-align: left;
    padding-left: 20px;
    color: #3B3667;
    font-weight: 300;
}
.celine-about-content-container {
    order: 1;
    height: 100%;
}